@import 'fonts';
@import "mq";
@import "mixin";
@import "function";
@import "animation";
//Colors
$deepPurple: #6A00EB;
$primaryPurple: #994DFF;
$secondaryPurple: #BB88FF;
$lightPurple: #B6B3CF;
$purpleGradient: linear-gradient(360deg, #994DFF 0%, #BB88FF 100%, #BB88FF 100%);

$deepOrange: #F26C05;
$primaryOrange: #FF7C4D;
$secondaryOrange: #FFA466;
$landingOrange: #E1742E;

$deepBlue: #1FD0FF;
$primaryBlue: #6CD2F2;
$secondaryBlue: #A0E5FB;
$primaryRed: #F05E7E;

$lightGrey: #E5E5E5;
$grey: #999999;
$darkGrey: #555555;

$assetPanelYellow: rgba(254, 203, 4, 0.5);
$assetPanelGreen: rgba(50, 195, 121, 0.5);
$assetPanelBlue: rgba(71, 175, 171, 0.5);
$assetPanelPurple: rgba(128, 1, 217, 0.5);
$assetPanelPink: rgba(241, 93, 119, 0.5);

$btnOrange: rgba(242, 108, 5, 0.5);

//Breakpoints
$iPadPro: 834px;
$iPad: 768px;
$iPadLandscape: 1024px;
/* event Page */
// Color
$bgGray: #F4F4F4;
$lineGray: #DBDBDB;
$lightTextGray: #BDBDBD;
$highlightYellow: #FFCC00;
$sendBtnGreen: #04b077;
$textGray: #707070;
// FontSize
$md-font: 16px;
$lg-font: 20px;
// Event Info
@mixin event-info($height: 450px){
  display: flex;
  width: 100%;
  height: $height;
  //box-shadow: 7px 7px 27px 7px rgba(0, 0, 0, 0.12);
  border-radius: 40px;
  box-sizing: border-box;
}

@mixin event-wrap($padding: 0 25%){
  position: relative;
  display: flex;
  width: 100vw;
  flex-flow: column;
  //max-width: $maxWidth;
  padding: $padding;
  box-sizing: border-box;
}

@mixin event-font($fontFamily:"Rix nooAriDuri", $size:16px, $weight: 500, $lineHight:inherit){
  font-family: $fontFamily;
  font-size: $size;
  font-weight: $weight;
  line-height: $lineHight;
  white-space: pre-line;
}
// Event Info End
%flex-center{
  display: flex;
  flex-flow: column;
  align-items: center;
}
%mobile-event{
  -webkit-tap-highlight-color: transparent;
}
a{
  @extend %mobile-event;
}

@mixin popingAnimation($duration) {
  animation: transparent $duration ease-in-out;
}

@keyframes transparent {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  50% {
    opacity: .5;
    transform: scale(1.2);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
$mobile: 767px;
$iphoneSEHeight: 568px;
$tabletFontSize: 0.75rem;