@import "../styles/fonts";
@import "../styles/animation";

.profileWrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.7);
    z-index: 50;
}

.profileHolder {
    position: absolute;
    top: 3%;
    left: 1.5%;
    width: calc(100% * 0.15);
    aspect-ratio: 7 / 2;
    z-index: 50;

    .profileCircle {
        position: absolute;
        height: 100%;
        aspect-ratio: 1 / 1;
        border-radius: 50%;
        border: 3px solid white;
        overflow: hidden;
        z-index: 51;
    }

    .profileCircle:hover {
        cursor: pointer;
    }

    .profilePanel {
        position: relative;
        top: 10%;
        left: 10%;
        width: 90%;
        aspect-ratio: 4 / 1;
        background-color: transparentize(#354053, 0.5);
        border-radius: 1vh 0.4vh;
        border: 1px solid black;
        transform: skew(-5deg);
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    .profilePanel:hover {
        cursor: pointer;
        outline: 5px solid #F5F3E4;
    }

    .thumbnail {
        position: absolute;
        top: 3px;
        left: 3px;
        height: 100%;
        aspect-ratio: 1/1;
        z-index: 51;
        border-radius: 50%;
        // object-fit: fill;
        object-fit: cover;
    }

    .thumbnail:hover {
        cursor: pointer;
        outline: 5px solid #F5F3E4;
    }

    .id {
        position: absolute;
        width: auto;
        aspect-ratio: 20 / 1;
        top: 40%;
        left: 30%;
        transform: translate(0%, -60%);
        font-size: 1.5vw;
        display: flex;
        flex-wrap: wrap;
        text-align: left;
        justify-content: left;
        align-items: left;
    }

    .expWrapper {
        position: absolute;
        top: 80%;
        left: 30%;
        transform: translate(0%, -60%);
        width: 60%;
        height: 30%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        border-radius: 0.5vh 0.25vh 0.5vh 0.25vh;
        border: 1.5px solid rgba(39, 23, 24, 1);
        background: rgba(39, 23, 24, 1);
    }

    .expIcon {
        position: absolute;
        transform: translate(-180%, 60%);
        object-fit: fill;
        max-width: 45%;
        max-height: 45%;
        z-index: 51;
    }

    .exp {
        position: absolute;
        top: 80%;
        left: 30%;
        transform: translate(0%, -60%);
        width: 30%;
        height: 30%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        border-radius: 0.5vh 0.25vh 0.5vh 0.25vh;
        border: 1.5px solid rgba(39, 23, 24, 1);
        background: #ff9100;

        .labelStyles {
            position: absolute;
            font-size: 80%;
            font-size: 1rem;
            left: 7%;
            margin-left: 5px;
        }
    }
}
.speechBubble {
    position: absolute;
    width: 3vw;
    aspect-ratio: 1/1;
    left: 17.5vw;
    top: 5vh;
    transform: scaleX(-1) rotate(-20deg);
    transition: transform 0.3s ease-out;
}

.speechBubble:hover {
    cursor: pointer;

    animation-name: scaleUpDown;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

@keyframes scaleUpDown {
    0% {
      transform: scale(1) scaleX(-1) rotate(-20deg);
    }
    50% {
      transform: scale(1.1) scaleX(-1) rotate(-20deg);
    }
    100% {
      transform: scale(1) scaleX(-1) rotate(-20deg);
    }
  }

.profileModal {
    position: absolute;
    width: calc(100% * 0.6);
    left: calc(100% * 0.2);
    aspect-ratio: 7 / 4;
    background: #F5F3E4;
    border-radius: 2vh 2vh;
    z-index: 51;
    display: flex;
    flex-direction: column;

    .modalHeader {
        position: absolute;
        width: 100%;
        aspect-ratio: 20 / 1;
        background: #ff9100;
        border-radius: 2vh 2vh 0px 0px;
        font-size: 1.5rem;
        display: flex;
        flex-wrap: wrap;
        text-align: center;
        justify-content: center;
        align-items: center;
        color: white;
    }

    .closeBtn {
        position: absolute;
        top: 0;
        right: calc(100% * 0.02);
        height: 100%;
        aspect-ratio: 1 / 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .closeBtn:before {
        content: "";
        display: block;
        padding-top: 100%;
    }

    .closeBtn:hover {
        cursor: pointer;
        @include hover-scale-animation;
    }

}

.closeImg {
    position: inherit;
    right: calc(100% * 0.02);
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.closeImg:hover {
    cursor: pointer;
    @include hover-scale-animation;
}

.nameChangeModal {
    position: absolute;
    width: 30%;
    left: 35%;
    aspect-ratio: 7 / 4;
    background: #F5F3E4;
    border-radius: 2vh 2vh;
    z-index: 51;
    display: flex;
    flex-direction: column;

    .nameChangeHeader {
        position: absolute;
        width: 100%;
        aspect-ratio: 10 / 1;
        background: #ff9100;
        border-radius: 2vh 2vh 0px 0px;
        font-size: 1.5rem;
        display: flex;
        flex-wrap: wrap;
        text-align: center;
        justify-content: center;
        align-items: center;
        color: white;
    }

    .rowHolder {
        position: absolute;
        top: calc(100% * 0.3);
        left: 5%;
        width: 80%;
        aspect-ratio: 10/1;
        background-color: #86849F;
        color: white;
        border-radius: 2vh;
        margin: 1vh;
        padding: 1vh;
        padding-left: 3vh;
        font-size: 1.5vw;
        text-align: left;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: left;
        align-items: center;
        justify-content: center;
        border: none;
        outline: none;
    }

    .rowHolder:focus {
        outline: 5px solid #F5F3E4;
    }

    .rowHolder::placeholder {
        color: white;
    }

    .confirmBtn {
        position: absolute;
        width: 30%;
        aspect-ratio: 5 / 2;
        bottom: calc(100% * 0.15);
        left: 35%;
        background-color: #FFD200;
        border-radius: 1.5vh 0.75vh 1.5vh 0.75vh;      
        transform: skew(-5deg);
        border: none;
        overflow: hidden;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        font-size: 4vh;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    }

    .confirmBtn:hover {
        cursor: pointer;
        outline: 5px solid #F5F3E4;
    }
}