@mixin fadeOut($name, $from, $to){
  @keyframes #{$name} {
    from { opacity: $from }
    to { opacity: $to }
  }
}

@mixin hover-scale-animation {
  transform-origin: center;
  animation: hover-scale 0.5s ease-in-out;

  @keyframes hover-scale {
    0% {
      transform: scale(1) ;
    }

    50% {
      transform: scale(1.2);
    }

    100% {
      transform: scale(1);
    }
  }
}


@mixin hover-scale-skew-animation {
  transform-origin: center;
  animation: hover-scale-skew 0.5s ease-in-out;

  @keyframes hover-scale-skew {
    0% {
      transform: scale(1) skew(-5deg);
    }

    50% {
      transform: scale(1.2) skew(-5deg);
    }

    100% {
      transform: scale(1) skew(-5deg);
    }
  }
}




