@import "../styles/fonts";
@import "../styles/animation";

.menuHolder {
    position: absolute;
    top: 3%;
    right: 1.5%;
    width: 6vw;
    padding-top: calc(100% * 0.001);
    aspect-ratio: 5 / 2;
    background-color: #354053;
    border-radius: 1vh 0.4vh;
    transform: skew(-5deg);
    border: none;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    .menuIcon {
        width: 2.5vw;
        aspect-ratio: 1/1;
        left: 5%;
        padding: 0.5vh;
    }
}

.menuHolder:hover {
    cursor: pointer;
    outline: 5px solid #F5F3E4;
    @include hover-scale-skew-animation;
}


.menuWrapper {
    position: absolute;
    width: 13%;
    height: 100%;
    right: 0;   
    z-index: 51;

    .menuPanel {
        position: relative;
        right: 0;
        background: #221C1C;
        width: 100%;
        height: 100%;
    }

    .triangleBG {
        position: absolute;
        right: 100%;
        border-left: 50px solid transparent; 
        border-bottom: 100vh solid #221C1C;
    }

    .closeBtn {
        position: absolute;
        width: auto;
        aspect-ratio: 1/1;
        right: 10%;
        top: 2%;
        z-index: 52;
    }

    .closeBtn:hover {
        cursor: pointer;
        @include hover-scale-animation;
    }

    .buttonContainer {      // 우측에서 좌측으로 열리는 애니메이션 필요함 
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-items: center;
        font-size: 1.2rem;

        .button {
            position: relative;
            top: 11%;
            width: calc(100% * 0.6);
            padding-top: calc(100% * 0.01);
            aspect-ratio: 10 / 3;
            background-color: #354053;
            border-radius: 10px 4px;
            transform: skew(-5deg);
            border: none;
            display: flex;
            text-align: center;
            align-items: center;
            justify-content: center;
            margin-bottom: 50px;
            color: white;
        }

        .button:hover {
            cursor: pointer;
            outline: 5px solid #F5F3E4;
        }

        div:nth-child(1){
            background-color: #354053;
        }
        div:nth-child(2){
            background-color: #354053;
        }
    }
}

.settingWrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.7);
    z-index: 51;
}

.settingModal {
    position: absolute;
    width: calc(100% * 0.6);
    left: calc(100% * 0.2);
    aspect-ratio: 7 / 4;
    background: #F5F3E4;
    border-radius: 2vh 2vh;
    z-index: 51;
    display: flex;
    flex-direction: column;

    .modalHeader {
        position: absolute;
        width: 100%;
        aspect-ratio: 20 / 1;
        background: #ff9100;
        border-radius: 2vh 2vh 0px 0px;
        font-size: 1.5rem;
        display: flex;
        flex-wrap: wrap;
        text-align: center;
        justify-content: center;
        align-items: center;
        color: white;
    }

    .closeBtn {   
        position: absolute;
        top: 0;
        right: calc(100% * 0.02);
        height: 100%;
        aspect-ratio: 1 / 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .closeBtn:before {
        content: "";
        display: block;
        padding-top: 100%;
    }
    .closeBtn:hover {
        cursor: pointer;
    }
    
    .closeImg {
        position: inherit;
        right: calc(100% * 0.02);
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
    }
    .closeImg:hover {
        cursor: pointer;
    }

    .menuTypesWrappr {
        position: absolute;
        width: 30%;
        height: 40%;
        top: calc(100% * 0.19);
        font-size: 5vh;
        left: calc(30% * 0.2);

        .menuTypeHolder {
            width:70%;
            height: 30%;
            border-radius: 1.5vh 0.75vh 1.5vh 0.75vh;
            left: calc(30% * 0.2);
            margin-bottom: 3%;
        }

        .dotList {
            position: absolute;
            margin-left: 5%;
            margin-top: 6%;
        }

        .menuType {
            position: absolute;
            width: 80%;
            margin-top: 6%;
            margin-left: -50%;
            text-align: left;
            list-style: none; 
            font-size: 2vw;
            display: inline-block;
            white-space: nowrap; // Prevents text from wrapping to multiple lines
            text-overflow: ellipsis;
        }

        .menuTypeHolder:hover {
            background-color: transparentize(#354053, 0.5);
            cursor: pointer;
        }

        .selected {
            background-color: #ff9100;
            cursor: pointer;
            list-style: none; 
            text-align:right;
            pointer-events: none;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
        }
    }

    li {
        cursor: pointer;
    }

    .menuContentsWrapper {
        position: absolute;
        width: 60%;
        height: 70%;
        top: calc(100% * 0.19);
        font-size: 4vh;
        left: calc(60% * 0.55);
        background-color: #87849F;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
        border-radius: 2vh;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-content: flex-start;

        .languageHolder {
            position: relative;
            top: 1vh;
            width: 40%;
            height: auto;
            background-color: #D9D8DE;
            color: #3F405F;
            border-radius: 1.5vh;
            margin: 1vh;
            padding: 1vh;
            text-align: center;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
        }

        .languageHolder:hover {
            cursor: pointer;
        }

        .languageSelected {
            box-shadow: 0 0 0 5px #ff9100;
        }

        .selectedLangBG{
            position: absolute;
            width: 10%;
            aspect-ratio: 1/1;
            top: 2.8%;
            left: 0;
            top: 0;
            background: #ff9100;
            border-radius: 1.3vh 0px;
            z-index: 51;

            .selectedLangImg {
                position: absolute;
                width: 90%;
                aspect-ratio: 1/1;
                left: 6%;
                top: 3.5%;
                z-index: 51;
            }
        }

        .rowHolder {
            position: relative;
            top: 1vh;
            width: 80%;
            height: auto;
            background-color: #D9D8DE;
            color: #3F405F;
            border-radius: 2vh;
            margin: 1vh;
            padding: 1vh;
            font-size: 1.5vw;
            text-align: left;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
            display: flex;
            justify-content: left;
            align-items: center;
            text-transform: uppercase;
            list-style: disc;
        }

        .bulletPoint {
            width: 0.5vw;
            height: 0.5vw;
            background-color: #3F405F;
            border-radius: 50%;
            margin-right: 1vw;
        }

        .sliderToggle {
            position: absolute;
            right: 3%;
            width: 15%;
            aspect-ratio: 2/1;
            border-radius: 3vh;
            background-color: #272638;
            cursor: pointer;
        }

        .slider {
            width: 40%;
            aspect-ratio: 1/1;
            border-radius: 50%;
            background-color: #DFDFDF;
            position: absolute;
            top: 10%;
            left: 5%;
            transition: transform 0.2s;
        }

        .sliderToggle.toggled {
            background-color: #FEC447;
        }

        .sliderToggle.toggled .slider {
            transform: translateX(125%);
        }

        .toggleOn {
            position: absolute;
            width: 70%;
            aspect-ratio: 1/1;
            left: 15%;
            top: 13%;
        }
        .toggleOff{
            position: absolute;
            width: 60%;
            aspect-ratio: 1/1;
            left: 17%;
            top: 17%;
        }

        
    }
}