@import "src/styles/variables";

.loadingContainer {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;

    &.notLoading {
        display: none;
    }

    .bg{
        height: 100%;
        min-width: 100%;
        position: relative;
    }
    
    .character{
        position: absolute;
        bottom: 6%;
        width: 50%;
        height: 50%;
    }    
}