.mySlider {
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 1rem;
    right: 3%;
    width: 70%;
}

input[type=range] {
    -webkit-appearance: none;
    background: transparent;
    width: 100%;
}


input[type=range]::-webkit-slider-runnable-track {
    height: 2vh;
    // background-color: #87849F;
    border-radius: 2.5vh;
    cursor: pointer;
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    position: relative;
    width: 2.8vh;
    height: 2.8vh;
    transform: translateY(-10%);
    z-index: 1;
    background-color: #3F405F !important;
    border: none;
    box-shadow: none;
    border-radius: 50%;
    cursor: pointer;
}


input[type=range]::-moz-range-thumb {
    width: 2vh;
    height: 2vh;
    background-color: #3F405F !important;
    border: none;
    border-radius: 50%;
    cursor: pointer;
}

p {
    width: 100%;
    font-size: 1vw;
    font-weight: bold;
    padding-right: 3%;
    text-align: right;
}
