@mixin font-rixnooAriDuri($family){
  @import url('https://fonts.googleapis.com/css2?family=#{$family}:wght@100;300;400;500;700;900&display=swap');
}


@font-face {
  font-family: 'Rix InooAriDuri';
  src:url("../Assets/fonts/RixInooAriDuri Regular.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: 'ONE Mobile POP';
  src:url("../Assets/fonts/ONE Mobile POP.ttf") format("truetype");
  font-weight: 500;
}


@include font-rixnooAriDuri('Rix InooAriDuri');
@include font-rixnooAriDuri('ONE Mobile POP');
