.snbMenu {
  position: fixed;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 5vh;
  color: white;
  width: auto;
  height: 60vh;

  .snbMenuItem {
    display: flex;
    align-items: center;
    padding-bottom: 3%;
    padding-top: 3%;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    width: 110%;
    height: 15%;
    max-height: 3rem;
    line-height: 1.2rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border-radius: 3vh;
  }

  @media (max-width: 650px) {
    .snbMenuItem {
      height: 8%;
      margin: 1%;
    }
  }

  @media (max-height: 450px) {
    .snbMenuItem {
      height: 6%;
      margin: 0.5%;
    }
  }

  @media (max-height: 350px) {
    .snbMenuItem {
      height: 5%;
      margin: 0.25%;
    }
  }

  .snbMenuItem:hover {
    background: rgba(53, 64, 83, 0.7);
    outline: 5px solid #F5F3E4;

  }

  .snbMenuItem:not(:last-child) {
    margin-bottom: 0.5rem;
  }

  .snbMenuIcon {
    width: 5.5vh;
    height: 5.5vh;
    margin-right: 0.5rem;
    padding-left: 0.1rem;
    transform: skew(10deg);
  }

  .snbMenuLabel {
    font-weight: bold;
    padding-left: 1rem;
  }
}