@import "../styles/fonts";

.chatHolder {
    position: absolute;
    bottom: 3%;
    left: 1.5%;
    padding-top: calc(100% * 0.001);
    width: 20vw;
    display: flex;
    border: none;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    border-radius: 5vh;
    background: rgba(53, 64, 83, 0.5);
    aspect-ratio: 8/1;
    border: 1px solid black;

    .imgholder {
        position: inherit;
        left: 15px;
    }
 
    img {
        position: absolute;
        width: 2vw;
        aspect-ratio: 1/1;
        top: 0;
        transform: translate(0%, -50%);
        object-fit: cover;
    } 

    .userId {
        position: absolute;
        width: 50px;
        left: 55px;
        border-right:1px groove; 
        padding: calc(100%*0.01);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 100%;
    }

    .chatText {
        position: absolute;
        width: 50%;
        left: 125px;
        display: inline-block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: left;
        margin-left: 0;
    }

    @media only screen and (max-width: 600px) { 
        .userId {
            left: 0;
            padding: 0;
            font-size: 0.8rem;
        }
    
        .chatText {
            width: 100%;
            left: 0;
            font-size: 0.8rem;
        }
    }
}

.chatHolder:hover {
    cursor: pointer;
    outline: 5px solid #F5F3E4;
}


