:lang(ko) * {
  font-family: 'Rix nooAriDuri' !important;
}
:lang(eng) * {
  font-family: 'Rix nooAriDuri' !important;
}
.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  box-sizing: border-box;
  flex-flow: column;
  overflow-x: hidden;

  main {
    display: flex;
    overflow: hidden;
    height: calc(100%);
    margin-top: 0rem;

    & > div {
      height: 100%;
      width: 100%;
      overflow-x: hidden;
      display: flex;
      align-items: center;
      flex-flow: row;
    }
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
