.gridContainer {
    display: inline-table;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;

    .grid {
        width: 100%;
        height: 100%;
        justify-content: space-around;
        align-items: center;
        padding-top: 4%;
        display: grid;
        overflow: hidden;
    }
}

.circle {
    width: calc(100% - 20px);
    aspect-ratio: 1/1;
    border-radius: 50%;
    margin: 3.5%;
    object-fit: cover;
    object-position: center;
    max-width: 13%;
    max-height: 12%;
    border: none;
    box-sizing: border-box;
    padding: 1px;
}

.lock{
    z-index: 1;
    object-fit: contain;
    transform: translate(-25%, 0%);
    margin-left: -12%;
    max-width: 13%;
    max-height: 12%;
}

.selected {
    margin-left: calc(3.5% - 1px * 10)/10;
    margin-right: calc(3.5% - 1px * 10)/10;
    border: none;
    box-sizing: border-box;
    padding: 1px;
    border-radius: 50%;
    box-shadow: 0 0 0 5px #ff9100;
}

.circle:hover {
    cursor: pointer;
}   
.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0;
}