html {
  font-size: 16px;
}

@media screen and (orientation:landscape) and (max-width: 860px), screen and (orientation: portrait) and (max-height: 860px) {
  html {
    font-size: 11px !important;
  }
}
@media screen and (orientation:landscape) and  (min-width: 860px) and (max-width: 1024px),
screen and (orientation:portrait) and  (min-height: 860px) and (max-height: 1024px) {
  html {
    font-size: 14px !important;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
