// #354053
@import "../styles/fonts";

.startBtn {
    position: absolute;
    bottom: 3%;
    right: 1.5%;
    width: calc(100% * 0.13);
    padding-top: calc(100% * 0.001);
    aspect-ratio: 5 / 2;
    background-color: #FFD200;
    border-radius: 1.5vh 0.75vh 1.5vh 0.75vh;
    transform: skew(-5deg);
    border: none;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    .startTxt {
        width: 100%;
        height: calc(100% - 20%);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 2.5vw;
        white-space: nowrap;
    }

    .bar {
        position: absolute;
        bottom: 7%;
        left: 5%;
        width: 90%;
        height: 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        border-radius: 5px 2.5px 5px 2.5px;
        background: rgba(0, 0, 0, 0.2);

        &.isStartBar {
            background-color: #268C7F;
        }
    }
}

.startBtn:after {
    position: absolute;
    content: "";
    width: 0;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -1;
    background-color: #ff9100;
    border-radius: 5px;
    transition: all 0.3s ease;
}

.startBtn:hover {
    cursor: pointer;
    outline: 5px solid #F5F3E4;
}

.startBtn:hover:after {
    left: 0;
    width: 100%;
    color: #FFD200;
}

.startBtn_active {
    position: absolute;
    bottom: 3%;
    right: 1.5%;
    width: calc(100% * 0.13);
    padding-top: calc(100% * 0.001);
    aspect-ratio: 5 / 2;
    background-color: #49B3A5;
    border-radius: 1.5vh 0.75vh 1.5vh 0.75vh;
    transform: skew(-5deg);
    border: none;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    .startTxt {
        width: 100%;
        height: calc(100% - 20%);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 2.5vw;
        white-space: nowrap;
    }

    .bar {
        position: absolute;
        bottom: 7%;
        left: 5%;
        width: 90%;
        height: 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        border-radius: 5px 2.5px 5px 2.5px;
        background: rgba(0, 0, 0, 0.2);
    }

    .circles li {
        position: absolute;
        display: block;
        list-style: none;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.2);
        animation: animate 25s linear infinite;
        bottom: -150px;

    }

    .circles li:nth-child(1) {
        left: 25%;
        width: 80px;
        height: 80px;
        animation-delay: 0s;
    }


    .circles li:nth-child(2) {
        left: 10%;
        width: 20px;
        height: 20px;
        animation-delay: 2s;
        animation-duration: 12s;
    }

    .circles li:nth-child(3) {
        left: 70%;
        width: 20px;
        height: 20px;
        animation-delay: 4s;
    }

    .circles li:nth-child(4) {
        left: 40%;
        width: 60px;
        height: 60px;
        animation-delay: 0s;
        animation-duration: 18s;
    }

    .circles li:nth-child(5) {
        left: 65%;
        width: 20px;
        height: 20px;
        animation-delay: 0s;
    }

    .circles li:nth-child(6) {
        left: 75%;
        width: 110px;
        height: 110px;
        animation-delay: 3s;
    }

    .circles li:nth-child(7) {
        left: 35%;
        width: 150px;
        height: 150px;
        animation-delay: 7s;
    }

    .circles li:nth-child(8) {
        left: 50%;
        width: 25px;
        height: 25px;
        animation-delay: 15s;
        animation-duration: 45s;
    }

    .circles li:nth-child(9) {
        left: 20%;
        width: 15px;
        height: 15px;
        animation-delay: 2s;
        animation-duration: 35s;
    }

    .circles li:nth-child(10) {
        left: 85%;
        width: 150px;
        height: 150px;
        animation-delay: 0s;
        animation-duration: 11s;
    }

    @keyframes animate {
        0% {
            transform: translateY(0) rotate(0deg);
            opacity: 1;
            border-radius: 0;
        }

        100% {
            transform: translateY(-1000px) rotate(720deg);
            opacity: 0;
            border-radius: 50%;
        }
    }
}

.startBtn_active:hover {
    cursor: pointer;
    outline: 5px solid #F5F3E4;
}
